/* Homepage styles */
#hero {
  padding: 60px 0 60px 0;
  position: relative;
  overflow: visible; /* Allow overflow to see the hero-layer */
  z-index: 10;
  min-height: 800px;
}

/* Video Background */
.hero-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  z-index: 0;
}

/* Dark overlay for better text readability */
#hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

/* Remove lines between sections */
.homepage-section {
  border: none !important;
  border-top: none !important;
  border-bottom: none !important;
  box-shadow: none !important;
}

.homepage-section + .homepage-section {
  border-top: none !important;
  margin-top: 0 !important;
}

/* Remove any default WordPress/Understrap borders */
hr, .hr {
  display: none !important;
}

.container hr, .row hr {
  display: none !important;
}

p {
    font-family: "Oswald", sans-serif;
    font-weight: 300;
    font-size: 27px;
    line-height: 1.3;
}

.hero-text-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; /* Center horizontally */
  text-align: center;
  width: 100%; /* Ensure it takes full width */
  position: relative;
  z-index: 20; /* Asegurar que est√© por encima del overlay */
}

.hero-text-container h1 {
    font-size: 49px;
    font-weight: 400;
    color: #fff;
    margin-top: 20px;
    position: relative;
    z-index: 20; /* Asegurar que el texto est√© por encima del overlay */
    font-family: "Bebas Neue", sans-serif;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hero-images {
  margin-bottom: 20px;
}

.bird-left, .bird-right {
  max-width: 50px;
  display: inline-block;
  margin: 0 10px;
}

.hero-image-container {
    position: absolute;
    top: 75%;
    right: 2.5%;
    transform: translateY(-50%);
    width: 270px;
    z-index: 20;
    pointer-events: none;
}

.hero-layer {
  width: 100%;
  height: auto;
  z-index: 20;
  position: relative;
}

#home-section-1 {
    padding-top: 50px;
    position: relative;
    z-index: 1;
}

/* Card link styles */
.card-link {
    border: 2px solid #000;
    display: block;
    margin-bottom: 30px;
    text-decoration: none;
    color: #fff;
    transition: transform 0.3s;
    position: relative;
    z-index: 10;
    overflow: hidden;
    border-radius: 0;
}

.card-link:hover {
    transform: translateY(-5px);
}

.card-link .card-header {
    background-color: #1abc9c; /* Teal color */
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 0;
}

.card-link h3 {
    margin: 0;
    font-size: 1.5rem;
    font-family: "Inter", sans-serif;
}

.card-link .arrow {
    font-size: 2rem;
    line-height: 1;
}

.card-link img {
    width: 400px !important;
    height: 360px !important;
    display: block;
    transition: transform 0.3s ease;
    -o-object-fit: cover;
    object-fit: cover;
    border-radius: 0;
}

.card-link:hover img {
    transform: scale(1.02);
}

.card-link:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 25px rgba(0,0,0,0.15);
}

/* Scrolling banner styles */
#scrolling-banner {
    background-color: #fef5e7; /* Light beige background */
    padding: 20px 0;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    z-index: 1;
}

#scrolling-banner .scrolling-banner-inner {
    display: inline-block;
    animation: scroll-left 20s linear infinite;
}

#scrolling-banner .scrolling-banner-content {
    display: inline-flex;
    align-items: center;
}

#scrolling-banner span {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
    margin: 0 20px;
}

#scrolling-banner img {
    height: 30px; /* Adjust height as needed */
    margin: 0 20px;
}

@keyframes scroll-left {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}


/* Content section styles */
.content-section {
    padding: 60px 0;
}

.content-section h2 {
    font-size: 2.2rem;
    font-weight: 800;
    margin-bottom: 20px;
    font-family: "Inter", sans-serif;
}

.image-frame {
    border: 2px solid #000;
    padding: 0;
}

.image-frame img {
