    left: 20px;
    width: 400px;
    height: 400px;
    z-index: 1;
    overflow: hidden;
}

.pattern-bg {
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
    object-fit: contain;
}

#home-section-6 {
