#home-form-section {
    padding: 60px 0 200px 0;
    position: relative;
}

#home-form-section .form-wrapper {
    background-color: #FAD3DB;
    border: 2px solid #000;
    padding: 0;
    position: relative;
    z-index: 2;
    overflow: hidden;
}

#home-form-section .form-text-col {
    background-color: #FAD3DB;
    padding: 50px 40px;
