/* Divider line between the two sections */
#home-form-section .form-wrapper::after {
    content: '';
    position: absolute;
    top: 0;
    left: 50%; 
    height: 100%;
    width: 2px;
    background-color: #000;
    z-index: 3;
}

/* Additional improvements for the form */
#home-form-section .form-wrapper {
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

