}

#home-form-section .form-fields-col {
    position: relative;
}

/* Styles for the placeholders */
.event-form input::-moz-placeholder, .event-form textarea::-moz-placeholder {
    color: #999;
    font-style: italic;
    font-weight: 600 !important;
}
.event-form input::placeholder,
.event-form textarea::placeholder {
    color: #999;
    font-style: italic;
    font-weight: 600 !important;
}

/* Styles for the select */
.event-form select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6,9 12,15 18,9'%3e%3c/polyline%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right 15px center;
    background-size: 16px;
    padding-right: 45px;
}

.event-form .form-group {
    margin-bottom: 1rem;
}

.event-form label {
    display: block;
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
    color: #fff;
    font-family: 'Quicksand', sans-serif;
    font-weight: 600;
}

.event-form input,
.event-form textarea,
.event-form select {
    width: 100%;
    padding: 10px 12px;
    border: 2px solid #000;
    background-color: #fff;
    font-family: 'Quicksand', sans-serif;
    font-size: 0.9rem;
    color: #000;
    border-radius: 0;
    transition: all 0.3s ease;
}

/* Estilos espec√≠ficos para los inputs de Name, Email y Tel√©fono - 60% de ancho */
.event-form .form-group:nth-child(1) input,
.event-form .form-group:nth-child(2) input,
.event-form .form-group:nth-child(3) input {
    width: 60%;
}

/* Mantener el resto de inputs al 100% */
.event-form .form-group:nth-child(4) select,
.event-form .form-group:nth-child(5) textarea,
.event-form .form-group:nth-child(6) input {
    width: 100%;
}

/* Estilos para el texto introductorio del formulario */
.form-intro-text {
    font-family: 'Quicksand', sans-serif;
    font-size: 1.2rem;
    font-weight: 500;
