    box-shadow: 0 0 0 3px rgba(249, 215, 28, 0.2);
}

.event-form textarea {
    font-size: 0.9rem;
    color: #777;
    resize: vertical;
    min-height: 60px;
}

/* El campo de fecha usa el icono nativo del navegador */

/* Estilos para campos con error */
.event-form input:invalid,
.event-form textarea:invalid,
.event-form select:invalid {
