    font-size: 1rem;
    color: #000;
    cursor: pointer;
    transition: all 0.3s ease;
    border-radius: 0;
}

.event-form button:hover {
    background-color: #f0d000;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

#home-form-section .form-background-pattern {
